


















































import { Component, Prop, Vue, Emit } from "vue-property-decorator";
import { getCollection, delCollection } from "../api";

@Component({
  components: {}
})
export default class Collection extends Vue {
  platform = "taobao";
  type = "store";
  page = 1;
  tableData: any[] = [];
  more = false;
  multipleSelection: any[] = [];
  onClick() {
    this.page = 1;
    this.getList();
  }
  async del(items: any[]) {
    await delCollection(
      {
        type: this.type,
        items
      },
      this.platform
    );
    this.getList();
  }
  getList() {
    this.tableData = [];
    getCollection({
      platform: this.platform,
      type: this.type,
      page: this.page
      // @ts-ignore
    }).then(({ more, items }) => {
      this.tableData = items;
      this.more = more;
      this.$nextTick(() => {
        // @ts-ignore
        this.$refs.tb.toggleAllSelection();
      });
    });
  }

  go(d: number) {
    this.page += d;
    this.getList();
  }
}
